import React from "react";
import {QuestionContainer} from "../Question/Question-container"
import {AnswerContainer} from "../Answer/Answer-container"
import {NextButton} from "../../../button/Button"
import {DisplayEndDialog} from "../Dialog/Dialog"
export class Test extends React.Component {
  constructor() {
    super()
    this.state = {
        imageLoading:false
    }
  }
  componentDidUpdate(){
   /// khi src thay doi thi hien loading
    this.setState({imageLoading:true})
    let img=document.querySelector(".question-img")
    img.onload=()=>{
      this.setState({imageLoading:false})
    }
   ///sau khi onload thi tat loading
  } 
  imageDisplay(){
    if(this.state.imageLoading){
      return null
    } else {
      if(this.props.qAndA.img){
        return (
          <img
            className="question-img"
            src={process.env.PUBLIC_URL + `/img/${this.props.qAndA.img}.jpg`}
            alt="question-img"
          />
        )
      } else return <p>Không có hình</p>
    }
    
  }
  render() {
    const style={
        display:"flex",
        justifyContent: "space-between",
        alignItems: "center"
      }
    
    const imageAnswer={
      display:"grid",
      gridTemplateColumns:"42vw 15vw"
    }
    const imageDisplay=this.imageDisplay()
      return (
        <div className="display-quizzes">
          {this.props.passState.dialog ? (
            <DisplayEndDialog
              passContinueButton={this.props.continueButton}
              passEndButton={this.props.endButton}
            ></DisplayEndDialog>
          ) : (
            <div></div>
          )}
          <div style={style}>
            <QuestionContainer
              passQuestionButton={this.props.questionButton}
              passStateTwo={this.props.passState}
              passTestLength={this.props.testLength}
            ></QuestionContainer>
            <NextButton
              passNextButton={this.props.nextButton}
              passStateTwo={this.props.passState}
              passTestLength={this.props.testLength}
            ></NextButton>
          </div>
          <p className="question">{this.props.qAndA.question}</p>
          <div style={imageAnswer}>
            <AnswerContainer
              passStateTwo={this.props.passState}
              passAnswerButton={this.props.answerButton}
              answers={this.props.qAndA.answers}
            ></AnswerContainer>
            <div className="questionImg">
              {imageDisplay}
              <p>Nhấn giữ hình để phóng lớn</p>
            </div>
          </div>
          
        </div>
      );
    }
  }